/**
 * Get the options from an interaction. Does not currently support subcommands/subcommand groups.
 */
export function getOptions(interaction) {
    if (!('options' in interaction.data) || interaction.data.options === undefined)
        return {};
    return interaction.data.options.reduce((acc, curr) => {
        acc[curr.name] = curr;
        return acc;
    }, {});
}
